// UpdateResultsModal.js
import React, { useState } from "react";
import { CheckCircle, AlertCircle, XCircle, Eye, X } from "lucide-react";

const UpdateResultsModal = ({ results, onClose }) => {
  const [activeTab, setActiveTab] = useState(null);
  const [showDetails, setShowDetails] = useState(false);

  const {
    updated,
    notFound,
    errors,
    updatedStudents,
    notFoundStudents,
    errorStudents,
  } = results;

  const tabs = [
    {
      key: "updated",
      label: "Successfully Updated",
      count: updated,
      color: "green",
      icon: CheckCircle,
      students: updatedStudents,
    },
    {
      key: "notFound",
      label: "Not Found",
      count: notFound,
      color: "yellow",
      icon: AlertCircle,
      students: notFoundStudents,
    },
    {
      key: "errors",
      label: "Update Errors",
      count: errors,
      color: "red",
      icon: XCircle,
      students: errorStudents,
    },
  ];

  const getColorClasses = (color) => {
    const colors = {
      green: {
        bg: "bg-green-50",
        text: "text-green-700",
        border: "border-green-200",
        icon: "text-green-500",
      },
      yellow: {
        bg: "bg-yellow-50",
        text: "text-yellow-700",
        border: "border-yellow-200",
        icon: "text-yellow-500",
      },
      red: {
        bg: "bg-red-50",
        text: "text-red-700",
        border: "border-red-200",
        icon: "text-red-500",
      },
    };
    return colors[color];
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-full max-w-2xl mx-4 max-h-[80vh] overflow-y-auto">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-semibold text-gray-800">
            Update Results
          </h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <X className="h-5 w-5" />
          </button>
        </div>

        {!showDetails ? (
          /* Summary View */
          <div className="space-y-4">
            <div className="text-center mb-6">
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                Update Summary
              </h3>
              <p className="text-gray-600">
                Total processed: {updated + notFound + errors} students
              </p>
            </div>

            {tabs.map((tab) => {
              const colors = getColorClasses(tab.color);
              const Icon = tab.icon;

              return (
                <div
                  key={tab.key}
                  className={`${colors.bg} ${colors.border} border rounded-lg p-4`}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <Icon className={`h-6 w-6 ${colors.icon}`} />
                      <div>
                        <h4 className={`font-medium ${colors.text}`}>
                          {tab.label}
                        </h4>
                        <p className={`text-sm ${colors.text} opacity-75`}>
                          {tab.count} students
                        </p>
                      </div>
                    </div>

                    {tab.count > 0 && (
                      <button
                        onClick={() => {
                          setActiveTab(tab);
                          setShowDetails(true);
                        }}
                        className={`${colors.text} hover:opacity-75 flex items-center space-x-1 text-sm`}
                      >
                        <Eye className="h-4 w-4" />
                        <span>View Names</span>
                      </button>
                    )}
                  </div>
                </div>
              );
            })}

            <div className="flex justify-end mt-6">
              <button
                onClick={onClose}
                className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700"
              >
                Close
              </button>
            </div>
          </div>
        ) : (
          /* Details View */
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <button
                onClick={() => setShowDetails(false)}
                className="text-purple-600 hover:text-purple-500 text-sm"
              >
                ← Back to Summary
              </button>
            </div>

            <div className="border rounded-lg">
              <div
                className={`${getColorClasses(activeTab.color).bg} px-4 py-3 border-b`}
              >
                <div className="flex items-center space-x-2">
                  <activeTab.icon
                    className={`h-5 w-5 ${getColorClasses(activeTab.color).icon}`}
                  />
                  <h3
                    className={`font-medium ${getColorClasses(activeTab.color).text}`}
                  >
                    {activeTab.label} ({activeTab.count})
                  </h3>
                </div>
              </div>

              <div className="max-h-64 overflow-y-auto">
                {activeTab.students && activeTab.students.length > 0 ? (
                  <div className="divide-y">
                    {activeTab.students.map((student, index) => (
                      <div key={index} className="px-4 py-3">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="font-medium text-gray-900">
                              {student.first_name} {student.last_name}
                            </p>
                            <p className="text-sm text-gray-600">
                              Registration:{" "}
                              {student.registration_number || "N/A"}
                            </p>
                            {student.branch && (
                              <p className="text-sm text-gray-600">
                                {student.branch} - Batch {student.batch_year}
                              </p>
                            )}
                          </div>

                          {activeTab.key === "errors" && student.error && (
                            <div className="text-right">
                              <p className="text-xs text-red-600">Error:</p>
                              <p className="text-xs text-red-600">
                                {student.error}
                              </p>
                            </div>
                          )}

                          {activeTab.key === "notFound" && (
                            <div className="text-right">
                              <p className="text-xs text-yellow-600">
                                Student not found
                              </p>
                            </div>
                          )}

                          {activeTab.key === "updated" && (
                            <div className="text-right">
                              <p className="text-xs text-green-600">
                                Successfully updated
                              </p>
                            </div>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="px-4 py-8 text-center text-gray-500">
                    No students in this category
                  </div>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default UpdateResultsModal;
